import {Cycanswer} from './cycanswer.model';
export class Question {
    
    private nl:string;
    private cycl:string;
    private answers:Cycanswer[];
    private index:string;
    
    constructor(naturalLang:string,cycLang:string,cycAnswers:Cycanswer[],index:string){
        this.nl = naturalLang;
        this.cycl = cycLang;
        this.answers = cycAnswers;
        this.index = index;
    }
}
